import React from 'react';
import { FaFacebook, FaInstagram, FaLinkedin } from 'react-icons/fa';
import { HiOutlineMail } from 'react-icons/hi';
import './Footer.css';

const Contact = (props) => {
    return (
        <a href={props.link} target={'_blank'} className={'contact-slot'}>
            <props.icon className={'icon'}/>
            <p className={'contactLink'}>{props.name}</p>
        </a>
    )
}

const Footer = () => {
    return (
        <div id={'contact'}>
            <Contact icon={FaFacebook} link={'https://www.facebook.com/nitro.nlp'} name={'/nitro.nlp'}/>
            <Contact icon={FaInstagram} link={'https://www.instagram.com/nitro_nlp/'} name={'@nitro_nlp'}/>
            {/*<Contact icon={FaLinkedin} link={} name={'/'}/>*/}
            <Contact icon={HiOutlineMail} link={'mailto:contact@nitronlp.rocks'} name={'contact@nitronlp.rocks'}/>
        </div>
    )
}

export default Footer;
